import { FormGroup, FormBuilder } from '@angular/forms';
import { Answer } from './../_models/answer';
import { Question } from '@app/_models';
import { Test } from '../_models/test';
import { Component, OnInit } from '@angular/core';
import { takeWhile, tap, timeInterval } from 'rxjs/operators';
import { interval, observable, timer } from 'rxjs';
observable;

@Component({
  templateUrl: './play-test.component.html',
  styleUrls: ['./play-test.component.scss'],
})
export class PlayTestComponent implements OnInit {
  test: Test;
  question: Question;
  userAnswer: Answer;
  questionAnswers: Answer[] = [];
  subscribeTimer = 0;
  afterAnswerTimeout = 1500;
  disabled = false;

  questions: Question[] = [
    {
      id: 1,
      question: '    ,  ,  ',
    },
    {
      id: 2,
      question: '     ',
    },
    {
      id: 3,
      question:
        '          ?',
    },
    {
      id: 4,
      question:
        '    ,            ?',
    },
  ];

  answers: Answer[] = [
    {
      questionId: 1,
      answer: '      .',
      correct: true,
    },
    {
      questionId: 1,
      answer: '    ,  ',
      correct: false,
    },
    {
      questionId: 1,
      answer: '  .',
      correct: true,
    },
    {
      questionId: 2,
      answer: '   .',
      correct: false,
    },
    {
      questionId: 2,
      answer:
        '         .',
      correct: true,
    },
    {
      questionId: 2,
      answer: '  .',
      correct: false,
    },
    {
      questionId: 3,
      answer: ' 16 ',
      correct: false,
    },
    {
      questionId: 3,
      answer: ' 18 .',
      correct: true,
    },
    {
      questionId: 3,
      answer: ' 19 .',
      correct: false,
    },
    {
      questionId: 3,
      answer: ' 21 .',
      correct: false,
    },
    {
      questionId: 4,
      answer:
        '       ,     .',
      correct: false,
    },
    {
      questionId: 4,
      answer: '    .',
      correct: true,
    },
    {
      questionId: 4,
      answer: '  ,  .',
      correct: false,
    },
  ];

  constructor(fb: FormBuilder) {}

  ngOnInit() {
    this.getNewQuestion();
  }

  getNewQuestion() {
    this.question = this.getQuestion();
    this.questionAnswers = this.getAnswers(this.question);
    this.disabled = false;
  }

  getQuestion() {
    return this.questions[Math.floor(Math.random() * this.questions.length)];
  }

  getAnswers(question: Question): Answer[] {
    return this.answers.filter((q) => q.questionId == question.id);
  }

  onClick() {
    if (!this.disabled) {
      this.disabled = true;
      console.log(this.userAnswer);

      let okJob = setTimeout(
        () => this.getNewQuestion(),
        this.afterAnswerTimeout
      );
    }
  }
}
